.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
	display:none;
  border-radius: 4px;
  position: absolute;
  right: -30px;
  top:25px;
  background-color: rgba(0,0,0,0.8);
  height:80px;
  overflow-x:hidden;
  overflow-y: scroll;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;

}
.dropdown:hover .dropdown-content{
	display:block;
}
.dropdown-content div span:hover {background-color: #ddd;}
